@import "variables";
@import "mixins";
.card-person {
  border-radius: 6px;

  .card-title {
    font-size: 1.2em;
    color: $txt-dark;
  }

  .card-text {
    font-size: 0.9em;
  }

  .card-info {
    .col {
      font-size: 0.9em;
    }
  }

  img {
    border: 1px solid $bkg-btn;
  }

  &:hover {
    background-image: url("/img/triangle.png");
    background-position: top right;
    background-size: 32px;
    background-repeat: no-repeat;
    background-color: $bkg-hvr;
    border: 1px solid $bkg-btn;
    @include box-shadow-normal();

    img {
      opacity: 0.5;
    }
  }
}
